document.addEventListener("DOMContentLoaded", () => {
	"use strict";

	showHeaderMenu();
	tabs();
	slideWirhArrows();

	function showHeaderMenu() {
		try {
			const openBtn = document.querySelector("[data-header-openBtn]");
			const closeBtn = document.querySelector("[data-header-closeBtn]");
			const modalBlock = document.querySelector("[data-header-modalBlock]");

			openBtn.addEventListener("click", () => {
				modalBlock.classList.remove("hidden");
				modalBlock.classList.add("show");
				document.body.style.overflow = "hidden";
			});

			closeBtn.addEventListener("click", () => {
				modalBlock.classList.remove("show");
				modalBlock.classList.add("hidden");
				document.body.style.overflow = "";
			});
		} catch (e) {
			console.log(e);
		}
	}

	function tabs() {
		try {
			const listTab = document.querySelector(".guarantees__list-tabs");
			const itemTab = document.querySelectorAll(".guarantees__item-tab");
			const contentTab = document.querySelectorAll(".guarantees__tab-content");

			hiddenTab();
			showTab();

			listTab.addEventListener("click", (e) => {
				const target = e.target.closest(".guarantees__item-tab");
				if (target && target.classList.contains("guarantees__item-tab")) {
					itemTab.forEach((item, i) => {
						if (target == item) {
							hiddenTab();
							showTab(i);
						}
					});
				}
			});

			function hiddenTab() {
				try {
					itemTab.forEach((btn, i) => {
						btn.classList.remove("active");
					});

					contentTab.forEach((content) => {
						content.classList.remove("show");
						content.classList.add("hidden");
					});
				} catch (e) {
					console.log(e);
				}
			}

			function showTab(i = 0) {
				try {
					contentTab[i].classList.remove("hidden");
					contentTab[i].classList.add("show");
					itemTab[i].classList.add("active");
				} catch (e) {
					console.log(e);
				}
			}
		} catch (e) {
			console.log(e);
		}
	}

	function slideWirhArrows() {
		try {
			const btnPrev = document.querySelector("[data-btn-prev]");
			const btnNext = document.querySelector("[data-btn-next]");
			const slideContent = document.querySelectorAll(".cases__slide");
			let slideIndex = 1;

			showSlides(slideIndex);

			function showSlides(n) {
				try {
					if (n > slideContent.length) {
						slideIndex = 1;
					}
					if (n < 1) {
						slideIndex = slideContent.length;
					}

					slideContent.forEach((item) => {
						item.classList.remove("show");
						item.classList.add("hidden");
					});
					slideContent[slideIndex - 1].classList.remove("hidden");
					slideContent[slideIndex - 1].classList.add("show");
				} catch (e) {
					console.log(e);
				}
			}

			function plusSlides(n) {
				showSlides((slideIndex += n));
			}

			btnPrev.addEventListener("click", () => {
				plusSlides(-1);
			});
			btnNext.addEventListener("click", () => {
				plusSlides(1);
			});
		} catch (e) {
			console.log(e);
		}
	}
});
